/**
 * GatherPress Calendar Block Editor Styles
 *
 * Styles defined in this file are applied exclusively within the
 * WordPress block editor. These styles help distinguish the editor
 * view from the frontend display and provide enhanced styling for
 * the template configuration area.
 *
 * @package GatherPressCalendar
 * @since 0.1.0
 */

/**
 * Editor-specific block styles
 */
.wp-block-telex-block-gatherpress-calendar {
	/* background-color: #fff;*/ 
}

/**
 * Template configuration area - visible in editor only
 * Styled to match the event popover for visual consistency
 */
.gatherpress-calendar__template-config {
	display: block;
	margin-block-start: var(--wp--preset--spacing--50, 2rem);
	
	/* Popover-style appearance - defaults that can be overridden by attributes */
	position: relative;
	background: var(--wp--preset--color--base, #fff);
	border: 1px solid var(--wp--preset--color--contrast, #ddd);
	border-radius: var(--wp--custom--border-radius--medium, 8px);
	box-shadow: 0 8px 24px rgba(0, 0, 0, 0.15);
	padding: var(--wp--preset--spacing--50, 1.5rem);
	
	/* Subtle hover effect */
	transition: box-shadow 0.3s ease, border-color 0.3s ease;
	
	&:hover {
		box-shadow: 0 12px 32px rgba(0, 0, 0, 0.2);
		border-color: var(--wp--preset--color--primary, #007cba);
	}
}

/**
 * Template configuration label
 * Styled to stand out similar to popover headers
 */
.gatherpress-calendar__template-label {
	margin-block: 0 var(--wp--preset--spacing--40, 1rem);
	font-weight: 600;
	font-size: var(--wp--preset--font-size--small, 0.875rem);
	color: var(--wp--preset--color--contrast, #1e1e1e);
	text-transform: uppercase;
	letter-spacing: 0.05em;
	padding-block-end: var(--wp--preset--spacing--30, 0.75rem);
	border-bottom: 1px solid var(--wp--preset--color--contrast, #e8e8e8);
}

/**
 * Inner blocks container within template config
 */
.gatherpress-calendar__template-config .gatherpress-calendar-template {
	display: block;
	
	/* Add spacing between blocks */
	> .block-editor-block-list__layout > .wp-block {
		margin-block-end: var(--wp--preset--spacing--30, 0.75rem);
		
		&:last-child {
			margin-block-end: 0;
		}
	}
	
	/* Style the blocks to look like they're part of a card */
	.wp-block {
		padding: var(--wp--preset--spacing--20, 0.5rem);
		border-radius: var(--wp--custom--border-radius--small, 4px);
		// background: var(--wp--preset--color--base-2, #fafafa);
		transition: background 0.2s ease;
		
		&:hover {
			background: var(--wp--preset--color--base, #fff);
		}
	}
}

/**
 * Editor calendar enhancements
 */
.editor-styles-wrapper {
	.gatherpress-calendar__table {
		th {
			background: #f0f0f1;
		}

		td.has-posts {
			background: #e7f5fe;
		}
	}

	.gatherpress-calendar__event {
		cursor: default;
		pointer-events: none;
	}
}

/**
 * Responsive adjustments for template config in editor
 */
@media (max-width: 768px) {
	.gatherpress-calendar__template-config {
		padding: var(--wp--preset--spacing--40, 1rem);
		margin-block-start: var(--wp--preset--spacing--40, 1rem);
	}
	
	.gatherpress-calendar__template-label {
		font-size: var(--wp--preset--font-size--small, 0.8125rem);
	}
}